<script>
import axios from "axios";
import PostListMain from "@/components/PostListMain.vue";

export default {
  components: {
    PostListMain,
  },
  data() {
    return {
      posts: [],
      postLoading: false,
      selectedSort: "",
      searchQuery: "",
      page: 1,
      limit: 20,
      totalPage: 0,
      interval: 20,
      timer: true,
      scrollOn: Boolean,
    };
  },
  methods: {
    startScroll() {
      this.scrollOn = true;
    },
    async fetchBeer() {
      console.log("fetching");
      try {
        this.postLoading = true;
        const res = await axios.get("https://api.punkapi.com/v2/beers", {
          params: {
            page: this.page,
            per_page: this.limit,
          },
        });
        this.posts = res.data;
      } catch (e) {
        console.error(e);
      } finally {
        this.postLoading = false;
      }
    },
    scroo(bool) {
      if (bool === true) {
        setInterval(() => {
          scrollWrapper.scrollBy(1, 0);
        }, 15);
      }
    },
  },
  mounted() {
    this.fetchBeer();
    this.startScroll();
    this.scroo();
  },
  computed: {},

  watch: {
    scrollOn(newValue) {
      console.log(newValue);
      this.scroo(newValue);
    },
    interval(newValue) {
      return this.timer;
    },
  },
};
</script>

